#home-tab a{
    float: left;
    color : rgb(93, 189, 64);
    padding:0 20px;
}
#home-tab{
    background-color:#f6f6f6;
    height:40px;
    line-height:40px;
}
#data-li{
    overflow: hidden;
    height:40px;
    line-height:40px;
    border-bottom:0.5px solid rgb(212, 212, 212);
}
#data-sp{
    background-color: yellowgreen;
    color:white;
    padding:0 5px;
    margin:0 15px;
    border-radius:5px;
}
#tab-li-a{
    float: left;
    border:0.5px solid rgb(177, 176, 176);
    padding: 0 10px;
    cursor: pointer;
}
#item-title:hover{
    cursor: pointer;
    text-decoration: underline;
}
#data-content{
    display: none;
    overflow: auto;
}