/* top */
*{
    padding: 0;
    margin:0;
}
#top{
    width:100%;
    height:50px;
    background-color:#444;
}
.container{
    max-width:1200px;
    margin:0 auto;
}
#img-top{
    width:120px;
    margin-top:7px;
    float: left;
}
#top-inp{
    border:none;
    float: left;
    background: #888;
    border-radius:15px;
    height:20px;
    width:200px;
    margin-top:15px;
    margin-left:10px;
}
li{
    list-style: none;
}
#top-ul a{
    color:rgb(196, 193, 193);
    text-decoration: none;
}
#top-ul li{
    float: right;
    line-height:50px;
    margin-left:50px;
}
/* top */

/* main */
#main{
    background-color: #e1e1e1;
    height:4000px;
    width:100%;
    padding-top:15px;
}
#main-left{
    width:850px;
    height:2400px;
    background-color: white;
    float: left;
}
#main-right{
    float: right;
    width:330px;
    height:2400px;
    background-color: white;
}
#m-r{
    height:40px;
    width:100%;
    background-color:#cac9c8;
    line-height:40px;
    text-align: center;
}
#r-img{
    margin-left:65px;
}
/* main */